body {
  margin: 0;
  font-family: 'Cinzel', 'Open Sans', sans-serif;
  background-color: #181033;
  color: white;
}

/* Navbar Styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  background-color: #181033;
  position: relative;
  z-index: 1000;
}

.logo {
  width: 150px;
}

.menu-toggle {
  display: none;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  z-index: 1100;
}

/* Desktop Navbar */
.nav-menu {
  display: flex;
  gap: 2rem;
}

.nav-menu a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

.nav-menu a:hover {
  color: #ddd;
}

/* Mobile Menu */
@media (max-width: 1024px) {
  .menu-toggle {
    display: block;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: #181033;
    padding: 2rem;
    position: fixed;
    top: 0;
    right: 0;
    width: 250px;
    height: 100vh;
    border-radius: 0 0 0 20px;
    box-shadow: -2px 0 15px rgba(0, 0, 0, 0.5);
    z-index: 1000;
    transition: transform 0.3s ease-in-out;
    transform: translateX(100%);
  }

  .nav-menu.open {
    transform: translateX(0);
  }

  .backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 900;
  }
}

/* Background Section */
.background {
  background-image:
    linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url('../public/Movie.jpg');
  background-size: cover;
  background-position: center;
  padding: 12rem 6rem;
  text-align: center;
  color: white;
}


.background h1 {
  font-size: 3rem;
  margin-bottom: 2rem;
}

.background button {
  background-color: #181033;
  color: white;
  padding: 1rem 2rem;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s;
}

.background button:hover {
  background-color: #573b7b;
}

/* Popular Films */
.popular-films {
  background-color: #b2959e;
  padding: 4rem 2rem;
  text-align: center;
}

.popular-films h2 {
  font-size: 2.5rem;
  margin-bottom: 3rem;
}

.film-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
}

.film-card {
  background-color: white;
  color: black;
  border-radius: 20px;
  overflow: hidden;
  text-align: left;
}

.film-card img {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.film-card .content {
  padding: 2rem;
}

.film-card button {
  background-color: #181033;
  color: white;
  width: 100%;
  padding: 1rem;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  margin-top: 1rem;
}

.film-card button:hover {
  background-color: #573b7b;
}

/* Footer */
footer {
  background-color: #181033;
  padding: 2rem;
  text-align: center;
  color: #ccc;
  font-size: 0.9rem;
}

.footer-logo {
  width: 120px;
  margin-bottom: 1rem;
}

.cookie-popup {
  position: fixed;
  bottom: 20px;
  left: 20px;
  right: 20px;
  background-color: rgba(0, 0, 0, 0.9);
  color: #fff;
  padding: 15px 20px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.5);
  font-family: sans-serif;
  z-index: 1000;
}
.cookie-popup button.accept-button {
  background-color: #4caf50;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
  font-weight: bold;
  transition: background-color 0.3s ease;
}
.cookie-popup button.accept-button:hover {
  background-color: #45a049;
}

